<template>
	<div>
		<headerNav title="账号管理"/>
		<van-cell-group>
			<van-cell title="修改基本信息" is-link to="/info/basic"/>
			<van-cell title="修改登录密码" is-link to="/info/password"/>
			<van-cell title="切换账号" is-link @click="signOutFunc"/>
		</van-cell-group>
	</div>
</template>

<script>
	import headerNav from '../../../components/header/nav'
	import {mapActions} from 'vuex';

	export default {
		name: 'UserInfoIndex',

		components: {
			headerNav,
		},

		data() {
			return {}
		},

		created() {},

		methods: {
			...mapActions(['signOut']),

			signOutFunc() {
				this.signOut();
				this.$router.replace('/login');
			},
		},
	}
</script>

<style>

</style>



